.wind {
  &__row {
    display: flex;
  }
  &__item {
    flex: 1;
  }
  &__hand {
    border-bottom: 0.2rem solid var(--color-grey-50);
    text-align: center;
    font-size: var(--font-size-14);
    padding-bottom: 1.6rem;
    &.left {
      color: var(--color-type-1);
    }
    &.right {
      color: var(--color-type-5);
    }
  }
  &__position {
    display: flex;
    justify-content: space-between;
    padding: 2rem 1rem;
    border-bottom: 0.1rem solid var(--color-grey-50);
    span {
      font-size: var(--font-size-16);
      color: var(--color-grey-500);
    }
    &:last-child {
      border-bottom: 0;
      padding-bottom: 1rem;
    }
  }
}