.block-ui {
  $self: &;
  border-radius: var(--border-radius-16);
  border: solid 0.1rem var(--color-grey-50);
  background-color: #fff;
  margin-bottom: 2rem;
  &.empty {
    height: 24rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__header {
    font-weight: 700;
    padding: 2rem;
    font-size: var(--font-size-18);
    color: var(--color-grey-500);
    border-bottom: 0.1rem solid var(--color-grey-50);
  }
  &__content {
    padding: 2rem;
    max-width: 48rem;
    margin: 0 auto;
  }
  &__chart {
    margin-bottom: 3rem;
  }
  &__text {
    text-align: center;
    font-size: var(--font-size-16);
    line-height: 1.6;
    color: var(--color-grey-500);
  }
}