.trend {
  margin-bottom: 2rem;
  border-bottom: solid 0.2rem var(--color-grey-50);
  &__btn {
    @extend .btn;
    background-color: transparent;
    border: solid 0.2rem transparent;
    text-align: left;
    &.health {
      color: var(--color-type-0);
      border-color: var(--color-type-0);
    }
    &.body {
      color: var(--color-type-1);
      border-color: var(--color-type-1);
    }
    &.rank {
      color: var(--color-type-2);
      border-color: var(--color-type-2);
    }
    &.is-active {
      color: #fff;
      pointer-events: none;
      &.health {
        background-color: var(--color-type-0);
      }
      &.body {
        background-color: var(--color-type-1);
      }
      &.rank {
        background-color: var(--color-type-2);
      }
    }
  }
  &__chart {
    margin-bottom: 1rem;
  }
  &__list {
    margin-bottom: 2rem;
  }
  &__data {
    display: none;
    &.health {
      color: var(--color-type-0);
    }
    &.body {
      color: var(--color-type-1);
    }
    &.rank {
      color: var(--color-type-2);
    }
  }
  &__row {
    display: flex;
    justify-content: space-between;
    padding: 2rem 1rem;
    span {
      font-size: var(--font-size-16);
    }
  }
}