.meridian {
  &__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem 1rem;
    margin-bottom: 2rem;
  }
  &__detail {
    padding-top: 1rem;
  }
  &__hand {
    display: flex;
    gap: 1rem;
    margin-bottom: 1rem;
  }
  &__btn-hand {
    @extend .btn;
    background-color: #eee;
    flex: 1;
    display: block;
    padding-right: 1.6rem;
    color: #B9B9B9;
    margin-bottom: 0;
    padding-top: 1.4rem;
    padding-bottom: 1.4rem;
    &.active {
      background-color: #666;
      color: #fff;
    }
    @include susy-breakpoint($s-320, $c8) {
      padding-top: 1rem;
      padding-bottom: 1rem;
    }
  }
  &__btn {
    @extend .btn;
    background-color: transparent;
    border: solid 0.2rem transparent;
    margin-bottom: 0;
    padding-right: 1.6rem;
    &.C0 {
      color: var(--color-type-0);
      border-color: var(--color-type-0);
    }
    &.C1 {
      color: var(--color-type-1);
      border-color: var(--color-type-1);
    }
    &.C2 {
      color: var(--color-type-2);
      border-color: var(--color-type-2);
    }
    &.C3 {
      color: var(--color-type-3);
      border-color: var(--color-type-3);
    }
    &.C4 {
      color: var(--color-type-4);
      border-color: var(--color-type-4);
    }
    &.C5 {
      color: var(--color-type-5);
      border-color: var(--color-type-5);
    }
    &.C6 {
      color: var(--color-type-6);
      border-color: var(--color-type-6);
    }
    &.C7 {
      color: var(--color-type-7);
      border-color: var(--color-type-7);
    }
    &.is-active {
      color: #fff;
      &.C0 {
        background-color: var(--color-type-0);
      }
      &.C1 {
        background-color: var(--color-type-1);
      }
      &.C2 {
        background-color: var(--color-type-2);
      }
      &.C3 {
        background-color: var(--color-type-3);
      }
      &.C4 {
        background-color: var(--color-type-4);
      }
      &.C5 {
        background-color: var(--color-type-5);
      }
      &.C6 {
        background-color: var(--color-type-6);
      }
      &.C7 {
        background-color: var(--color-type-7);
      }
    }
  }
  &__header {
    display: flex;
    border-bottom: .1rem solid var(--color-grey-50);
    span {
      flex: 1;
      font-size: var(--font-size-14);
      color: var(--color-grey-500);
      padding: 1.6rem 0;
      text-align: center;
    }
  }
  &__item {
    display: flex;
    border-bottom: .1rem solid var(--color-grey-50);
    &:last-child {
      border-bottom: 0;
    }
    &.C0 {
      color: var(--color-type-0);
    }
    &.C1 {
      color: var(--color-type-1);
    }
    &.C2 {
      color: var(--color-type-2);
    }
    &.C3 {
      color: var(--color-type-3);
    }
    &.C4 {
      color: var(--color-type-4);
    }
    &.C5 {
      color: var(--color-type-5);
    }
    &.C6 {
      color: var(--color-type-6);
    }
    &.C7 {
      color: var(--color-type-7);
    }
    span {
      flex: 1;
      font-size: var(--font-size-14);
      padding: 1.6rem 0;
      text-align: center;
    }
  }
}