.measure-filter {
    $self: &;
    &__wrap {
        justify-content: flex-start;
        position: relative;
        border-radius: var(--border-radius-16);
        border: solid 0.1rem var(--color-grey-50);
        background-color: #fff;
        overflow: hidden;
        margin-bottom: 2rem;
        display: flex;
        gap: 1.6rem;
        padding: 2rem;
    }
    &__switch {
        display: flex;
        justify-content: center;
        gap: 0.8rem;
        align-items: center;
        & > h4 {
            font-weight: 500;
            color: var(--color-grey-500);
            font-size: 1.6rem;
            flex: 1;
        }
    }
    &__switch-inner {
        display: block;
        width: 56px;
        height: 32px;
        position: relative;
        input {
            opacity: 0;
            width: 0;
            height: 0;
            background-color: var(--color-main);
            &:checked + #{$self}__slider {
                background-color: var(--color-main);
                #{$self}__on {
                    display: block;
                }
                #{$self}__off {
                    display: none;
                }
            }
            &:focus + #{$self}__slider {
                box-shadow: 0 0 1px var(--color-main);
            }
            &:checked + #{$self}__slider {
                &::before {
                    transform: translateX(24px);
                }
            }
        }
    }
    &__slider {
        position: absolute;
        cursor: pointer;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: #ccc;
        transition: .4s;
        &::before {
            position: absolute;
            content: "";
            height: 24px;
            width: 24px;
            left: 4px;
            bottom: 4px;
            background-color: white;
            transition: .4s;
        }
        &.round {
            border-radius: 24px;
            &::before {
                border-radius: 50%;
            }
        }
    }
    &__on {
        display: none;
    }
    &__on,
    &__off {
        color: white;
        position: absolute;
        transform: translate(-50%,-50%);
        top: 50%;
        left: 50%;
        font-size: 1.2rem;
    }
    &__btn-more {
        border: 0;
        width: 2.4rem;
        height: 2.4rem;
        right: 2rem;
        top: 50%;
        transform: translate3d(0, -50%, 0);
        position: absolute;
        background-color: transparent;
        background-image: url("data:image/png;base64,");
        background-size: cover;
    }
}