.type-detail {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 500;
  opacity: 0;
  transition: all 500ms ease-in-out;
  pointer-events: none;
  &.is-active {
    opacity: 1;
    pointer-events: auto;
  }
  &__overlay {
    background-color: rgba(0,0,0,0.8);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__content {
    border-radius: var(--border-radius-16);
    background-color: #fff;
    padding: 3rem 4rem;
    width: 76.8rem;
    box-sizing: border-box;
    position: relative;
    @include susy-breakpoint($s-320, $c8) {
      width: calc(100% - 4rem);
      padding: 2rem;
    }
  }
  &__btn-close {
    position: absolute;
    width: 3rem;
    height: 3rem;
    border: 0;
    background-color: transparent;
    right: 0;
    top: -4rem;
    &::before,
    &::after {
      pointer-events: none;
      width: 3rem;
      height: 0.1rem;
      position: absolute;
      left: 50%;
      top: 50%;
      content: "";
      background-color: #fff;
    }
    &::before {
      transform: translate3d(-50%,-50%,0) rotate(45deg);
    }
    &::after {
      transform: translate3d(-50%,-50%,0) rotate(-45deg);
    }
  }
}

.type-info {
  text-align: center;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  @include susy-breakpoint($s-320, $c8) {
    height: 56vh;
  }
  &__title {
    text-align: center;
    font-size: var(--font-size-16);
    color: var(--color-grey-500);
    line-height: 1.6;
    margin-bottom: 1rem;
  }
  &__name {
    display: inline-block;
    font-size: var(--font-size-16);
    border: solid 0.2rem transparent;
    padding-left: 0.6rem;
    margin-bottom: 1.6rem;
    border-radius: var(--border-radius-8);
    padding: 1rem 2rem;
    color: #fff;
    background-color: var(--color-main);
    &.C0 {
      background-color: var(--color-type-0);
    }
    &.C1 {
      background-color: var(--color-type-1);
    }
    &.C2 {
      background-color: var(--color-type-2);
    }
    &.C3 {
      background-color: var(--color-type-3);
    }
    &.C4 {
      background-color: var(--color-type-4);
    }
    &.C5 {
      background-color: var(--color-type-5);
    }
    &.C6 {
      background-color: var(--color-type-6);
    }
    &.C7 {
      background-color: var(--color-type-7);
    }
  }
  &__text {
    font-size: var(--font-size-16);
    line-height: 1.6;
    color: var(--color-grey-500);
    text-align: left;
  }
}
