.date-picker {
  &__wrap {
    position: relative;
    border-radius: var(--border-radius-16);
    border: solid 0.1rem var(--color-grey-50);
    background-color: #fff;
    overflow: hidden;
    margin-bottom: 2rem;
    &::after {
      top: 50%;
      transform: translate3d(0,-50%,0);
      width: 2.4rem;
      height: 2.4rem;
      position: absolute;
      right: 2rem;
      content: "";
      background-image: url("data:image/png;base64,");
      background-size: cover;
      pointer-events: none;
    }
    input {
      text-align: left;
      font-size: var(--font-size-16);
      display: block;
      width: 100%;
      padding: 2rem 2rem;
      line-height: 1.5;
      color: var(--color-grey-500);
      background-color: #fff;
      box-sizing: border-box;
      border: 0;
    }
  }
}