.physique-type-list {
  &__btn {
    @extend .btn;
    text-align: left;
    &::after {
      width: 2rem;
      height: 2rem;
      background-image: url('data:image/png;base64,');
      position: absolute;
      right: 1.6rem;
      top: 50%;
      transform: translate3d(0,-50%,0);
      content: "";
      background-size: 2rem;
    }
    &:last-child {
      margin-bottom: 0;
    }
    &.type-0 {
      background-color: var(--color-type-0);
    }
    &.type-1 {
      background-color: var(--color-type-1);
    }
    &.type-2 {
      background-color: var(--color-type-2);
    }
    &.type-3 {
      background-color: var(--color-type-3);
    }
    &.type-4 {
      background-color: var(--color-type-4);
    }
    &.type-5 {
      background-color: var(--color-type-5);
    }
    &.type-6 {
      background-color: var(--color-type-6);
    }
    &.type-7 {
      background-color: var(--color-type-7);
    }
    &.type-8 {
      background-color: var(--color-type-8);
    }
    &.type-9 {
      background-color: var(--color-type-9);
    }
    &.type-10 {
      background-color: var(--color-type-10);
    }
    &.type-11 {
      background-color: var(--color-type-11);
    }
    &.type-12 {
      background-color: var(--color-type-12);
    }
    &.type-13 {
      background-color: var(--color-type-13);
      pointer-events: none;
      &::after {
        display: none;
      }
    }
  }
}
