:root {
    font-size: 62.5%;
    --color-bg: #F7F7F7;
    --color-grey-50: #eeeeee;
    --color-grey-500: #666666;
    --color-main: #4EACE9;
    --color-type-0: #8DD8E0;
    --color-type-1: #8DBDE0;
    --color-type-2: #8D9AE0;
    --color-type-3: #B38DE0;
    --color-type-4: #DD8DE0;
    --color-type-5: #E08DA7;
    --color-type-6: #E0A08D;
    --color-type-7: #E0BA8D;
    --color-type-8: #E0D08D;
    --color-type-9: #CDE08D;
    --color-type-10: #A3E08D;
    --color-type-11: #8DE09D;
    --color-type-12: #8DE0D0;
    --color-type-13: #999999;
    --font-size-20: 2rem;
    --font-size-18: 1.8rem;
    --font-size-16: 1.6rem;
    --font-size-14: 1.4rem;
    --border-radius-16: 1.6rem;
    --border-radius-8: 0.8rem;
}

